import React from "react";
import { NavLink } from "react-router-dom";
import styles from "./footer.module.css";

export default function Footer() {
    return (
        <div className={styles.footer}>
            <img className={styles.logo} src="https://res.cloudinary.com/manglit/image/upload/v1640106848/assets/logo_nlsu44.png" alt="mangalit" />
            <p>   ?   : themangalit@gmail.com</p>
            <div className={styles.links}>
                <a href="https://www.instagram.com/mangal_it" title="">
                    {/*  */}
                    <img src="https://res.cloudinary.com/manglit/image/upload/v1640262810/assets/inst_ghjerq.png" alt="" />
                </a>
                <a href="https://www.facebook.com/STMangalit" title="">
                    {/*  */}
                    <img src="https://res.cloudinary.com/manglit/image/upload/v1640263095/assets/facebooklogo_qog6ke.png" alt="" />
                </a>
                <NavLink title="" activeClassName="activeNav" to="/about">
                    
                </NavLink>
            </div>
            <p>&copy;    </p>
        </div>
    )
}
