import React, { Component } from "react";
import PropTypes from "prop-types";
import styles from "./chatbot.module.css";
import { ThemeProvider } from "styled-components";

import ChatBot from "react-simple-chatbot";

// all available props
const theme = {
  background: "#ebe6e6",
  fontFamily: "Arial",
  headerBgColor: "#b89494",
  headerFontColor: "#fff",
  headerFontSize: "15px",
  botBubbleColor: "#a08c8c",
  botFontColor: "#fff",
  userBubbleColor: "#c98282",
  userFontColor: "#fff",
};

class Review extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      men: "",
      women: "",
      withPitot: "",

      //gender: "", //comment out this 2 lines
      //age: "",
    };
  }

  componentWillMount() {
    const { steps } = this.props;
    const { name, men, women, withPitot } = steps;

    this.setState({ name, men, women, withPitot });
  }

  render() {
    const { name, men, women, withPitot } = this.state;
    let meatQuantity = 0;
    if (withPitot.value === "") {
      meatQuantity = parseInt(men.value) * 0.6 + parseInt(women.value) * 0.45;
    }
    if (withPitot.value === "") {
      meatQuantity = parseInt(men.value) * 0.7 + parseInt(women.value) * 0.55;
    }
    return (
      <div style={{ width: "100%" }}>
        <h3 dir="rtl"></h3>
        <table dir="rtl">
          <tbody>
            <tr>
              <td className={styles.td} dir="rtl">
                
              </td>
              <td className={styles.td} dir="rtl">
                {name.value}
              </td>
            </tr>
            <tr>
              <td className={styles.td} dir="rtl">
                 
              </td>
              <td className={styles.td} dir="rtl">
                {men.value}
              </td>
            </tr>
            <tr>
              <td className={styles.td} dir="rtl">
                 
              </td>
              <td className={styles.td} dir="rtl">
                {women.value}
              </td>
            </tr>
            <tr>
              <td className={styles.td} dir="rtl">
                 
              </td>
              <td className={styles.td} dir="rtl">
                {withPitot.value}
              </td>
            </tr>
            <tr>
              <td className={styles.total} dir="rtl">
                   (")
              </td>
              <td className={styles.total} dir="rtl">
                {meatQuantity.toFixed(2)}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}
Review.propTypes = {
  steps: PropTypes.object,
};

Review.defaultProps = {
  steps: undefined,
};

class SimpleForm extends Component {
  render() {
    return (
      <ThemeProvider theme={theme}>
        <ChatBot
          placeholder="/   "
          headerComponent={
            <div className={styles.header} dir="auto">
              
            </div>
          }
          floatingStyle="right"
          steps={[
            {
              id: "0",
              message:
                "  , '-        ",
              trigger: "1",
            },
            {
              id: "1",
              message: "?  ",
              trigger: "name",
            },
            {
              id: "name",
              user: true,
              trigger: "3",
            },
            {
              id: "3",
              message:
                "  {previousValue}  :) , ,            .",
              trigger: "4",
            },
            {
              id: "4",
              message: "?  ",
              trigger: "men",
            },
            {
              id: "men",
              user: true,
              validator: (value) => {
                if (isNaN(value)) {
                  return "  ";
                }
                if (value < 0) {
                  return "     0"
                }
                if (value % 1 !== 0) {
                  return "   "
                }
                return true;
              },
              trigger: "6",
            },
            {
              id: "6",
              message: "?  ",
              trigger: "women",
            },
            {
              id: "women",
              user: true,
              validator: (value) => {
                if (isNaN(value)) {
                  return "  ";
                }
                if (value < 0) {
                  return "   "
                }
                if (value % 1 !== 0) {
                  return "   "
                }
                return true;
              },
              trigger: "8",
            },
            {
              id: "8",
              message: "?     ",
              trigger: "withPitot",
            },
            {
              id: "withPitot",
              options: [
                { value: "", label: "", trigger: "10" },
                { value: "", label: "", trigger: "10" },
              ],
            },
            {
              id: "10",
              message: ",     ",
              trigger: "review",
            },
            {
              id: "review",
              component: <Review />,
              asMessage: true,
              trigger: "update",
            },
            {
              id: "update",
              message: "?    ",
              trigger: "update-question",
            },
            {
              id: "update-question",
              options: [
                { value: "", label: "", trigger: "update-yes" },
                { value: "", label: "", trigger: "end-message" },
              ],
            },
            {
              id: "update-yes",
              message: "   ?",
              trigger: "update-fields",
            },
            {
              id: "update-fields",
              options: [
                { value: "", label: "", trigger: "update-name" },
                { value: "", label: "", trigger: "update-men" },
                { value: "", label: "", trigger: "update-women" },
                {
                  value: " ",
                  label: " ",
                  trigger: "update-withPitot",
                },
              ],
            },
            {
              id: "update-name",
              update: "name",
              trigger: "10",
            },
            {
              id: "update-men",
              update: "men",
              trigger: "10",
            },
            {
              id: "update-women",
              update: "women",
              trigger: "10",
            },
            {
              id: "update-withPitot",
              update: "withPitot",
              trigger: "10",
            },
            {
              id: "end-message",
              message: "  ",
              end: true,
            },
          ]}
        ></ChatBot>
      </ThemeProvider>
    );
  }
}
export default SimpleForm;
